import React from "react";
import { Row, Col, Button } from "antd";
import { Link } from "react-router-dom";
import HeadingSubs from '../../../components/HeadingSubs'
import ThankyouDefaultIcon from '../assets/ic-thankyou-default.svg'
import ThankyouRegisterIcon from '../assets/ic-thankyou-register.svg'
import ThankyouBorrowerIcon from '../assets/ic-thankyou-borrower.svg'
import Spacer from '../../../components/Spacer'
import getQueryString from "../../../helpers/getQueryString";

interface Location {
  search: string;
  pathname: string;
}

type Props = {
  location: Location;
};

const Forms: React.FC<Props> = ({ location }) => {
  const topic = getQueryString(location?.search, "topic");
  let icon = ThankyouDefaultIcon;
  let title = "Terima Kasih";
  let subtitle = "";
  let link = <Link to='/'>Kembali ke homepage</Link>;
  switch(topic) {
    case 'lender':
      icon = ThankyouRegisterIcon;
      title = "Akun Mekar Anda telah aktif";
      subtitle = "Mohon lengkapi profil untuk memulai pendanaan Anda.";
      link = <Link to='/'>Kembali ke homepage</Link>;
    break;
    case 'borrower': 
      icon = ThankyouBorrowerIcon;
      title = "Data Anda telah kami simpan";
      subtitle = "Saat fitur ini telah tersedia, kami akan mengirimkan email pemberitahuan.";
      link = <Link to='/'>Kembali ke homepage</Link>;
    break;
  }

  return (
    <>
      <Row style={{ textAlign: 'center' }}>
        <Col xs={24}>
            <img src={icon} alt="icon"/>
        </Col>
      </Row>
      <Spacer setHeight='50px' />
      <div style={{ textAlign: 'center' }}>
        <HeadingSubs 
          title={title}
          subtitle={subtitle}
          alignment='center'
        />
      </div>
      <Spacer setHeight='40px' />
      <Row>
        <Col xs={24}>
          <Button type="primary" block>{link}</Button>
        </Col>
      </Row>
    </>
  );
};

export default Forms;
